@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
  margin: 0 auto;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
body {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}
.visually-hidden:not(:focus):not(:active),
input[type="radio"].visually-hidden,
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 100%;
}
.page-header__controls {
  padding: 22px 31px 22px 117px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 1px 0 0 #ececec, inset 0px 1px 0 0 #ececec;
}
.page-header__logo-image {
  display: none;
}
.page-header__logo-image--mobile {
  display: block;
}
.page-header__toggle {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
}
.page-header__toggle-image {
  display: block;
}
.page-header__toggle-image--close {
  display: none;
}
.page-header__toggle.menu-open .page-header__toggle-image {
  display: none;
}
.page-header__toggle.menu-open .page-header__toggle-image--close {
  display: block;
}
.page-header__toggle.menu-open--default {
  display: none;
}
.page-header__nav {
  display: none;
}
.page-header__nav.menu-open--default {
  display: block;
}
.page-header__nav.menu-open {
  width: 100%;
  display: block;
  position: absolute;
  background-color: white;
}
.page-header__nav-list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.page-header__nav-link {
  display: block;
  padding-left: 75px;
  position: relative;
  line-height: 79px;
  text-align: left;
  width: 100%;
  text-decoration: none;
  color: black;
  font-weight: 700;
  box-shadow: 0px 1px 0 0 #ececec, inset 0px 1px 0 0 #ececec;
}
.page-header__nav-image {
  position: absolute;
  top: 31px;
  left: 30px;
  opacity: 0.2;
}
.product-week {
  background: url("../img/bg-zigzag-line.svg") -5% 100% no-repeat;
  background-size: 100%;
}
.product-week__name {
  margin: 0;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
}
.product-week__label {
  margin: 0;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
}
.product-week__description {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: #444444;
}
.product-week__list {
  margin: 0;
  padding: 0;
  padding-bottom: 28px;
  font-size: 15px;
  line-height: 20px;
  text-align: left;
  display: table;
  color: #444444;
}
.product-week__row {
  display: table-row;
}
.product-week__cell {
  display: table-cell;
  border-bottom: 1px solid #ececec;
}
.product-week__option {
  padding-right: 31px;
}
.product-week__cost {
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
  color: #444444;
  font-weight: 700;
}
.product-week__buy {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  width: 260px;
  font-size: 17px;
  line-height: 50px;
  text-align: center;
  color: #444444;
  font-weight: 700;
  border: solid 2px #444444;
  background-color: transparent;
  margin-bottom: 36px;
}
.about {
  padding-top: 10px;
}
.about__header {
  font-size: 25px;
  text-align: left;
  line-height: 30px;
  color: black;
}
.about__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.about__item-image {
  fill: #63d1bb;
}
.about__item-content {
  font-size: 17px;
  line-height: 20px;
  text-align: left;
  color: black;
  font-weight: 700;
}
.reviews {
  padding-bottom: 20px;
  background: url("../img/bg-zigzag-line.svg") -5% 100% no-repeat;
  background-size: 100%;
}
.reviews__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.reviews__header {
  font-size: 25px;
  line-height: 30px;
  text-align: left;
  color: black;
  font-weight: 700;
}
.reviews__item-content {
  font-size: 15px;
  line-height: 30px;
  text-align: left;
  color: #444444;
}
.reviews__item-name {
  font-size: 17px;
  line-height: 24px;
  text-align: left;
  color: black;
  font-weight: 700;
}
.reviews__item-insta {
  font-size: 17px;
  line-height: 30px;
  text-align: left;
  color: #444444;
  font-weight: 400;
}
.contacts__term {
  width: 48px;
  margin-right: 31px;
  font-size: 15px;
}
.contacts__definition {
  text-align: left;
  margin: 0;
}
.contacts__map-wrapper {
  height: 457px;
  background: url("../img/icon-map-pin.svg") 55% 40% no-repeat, url("../img/map-mobile@1x.jpg") no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .contacts__map-wrapper {
    background: url("../img/icon-map-pin.svg") 55% 40% no-repeat, url("../img/map-mobile@2x.jpg") no-repeat;
    background-size: auto, 100%;
  }
}
.contacts__list {
  margin: 0;
  padding: 0;
  display: table;
  padding-bottom: 20px;
}
.contacts__row {
  display: table-row;
}
.contacts__cell {
  display: table-cell;
}
.contacts__option {
  padding-right: 32px;
}
.page-main__header {
  width: 100%;
  padding: 76px 39px 74px 39px;
  margin: 0;
  text-align: center;
  font-size: 27px;
  font-weight: bold;
  color: black;
  line-height: 30px;
  word-wrap: initial;
}
.page-main__header--index {
  padding: 76px 39px 120px 39px;
  background: url("../img/hero-mobile@1x.jpg") center 13% no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-main__header--index {
    background: url("../img/hero-mobile@2x.jpg") center no-repeat;
    background-size: 200%;
  }
}
.page-main__categories-list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.page-main__categories-link {
  display: block;
  padding: 30px;
  position: relative;
  text-align: left;
  background-color: #63d1bb;
  text-decoration: none;
  font-size: 19px;
  color: white;
  line-height: 24px;
  font-weight: 700;
}
.page-main__categories-link--dark {
  background-color: #56c3b2;
}
.page-main__categories-image {
  position: absolute;
  top: 23px;
  right: 36px;
  fill: #149080;
  opacity: 0.5;
}
.page-main__categories-image--dark {
  position: absolute;
  top: 21px;
  right: 28px;
  fill: #149080;
  opacity: 0.5;
}
